<template>
  <div class="info">
    <h1>项目经验/ProjectExperience</h1>
    <transition
      :duration="{ enter: 2500, leave: 1500 }"
      enter-active-class="animated fadeInRight">
      <div class="cont"  v-if="show">
        <el-row :gutter="20">
          <template>
            <swiper :options="swiperOption">
              <swiper-slide v-for="item in profiles" :key="item.name">
                <div class="item">
                  <h2 v-text="item.name"></h2>
                  <p v-text="'技术栈: ' + item.skills"></p>
                  <p v-text="item.content"></p>
                  <p v-text="item.content_tit"></p>
                  <p v-text="item.content1"></p>
                  <p v-text="item.content2"></p>
                  <p v-text="item.content3"></p>
                </div>
              </swiper-slide>
              <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
          </template>
        </el-row>
      </div>
    </transition>
  </div>
</template>
<script>

import 'swiper/dist/css/swiper.css'

import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
  data () {
    return {
      swiperOption: {
          pagination: {
            el: '.swiper-pagination'
          },
          loop : true,
          effect : 'coverflow',
          slidesPerView: 3,
          centeredSlides: true,
          coverflowEffect: {
            rotate: 30,
            stretch: 10,
            depth: 60,
            modifier: 2,
            slideShadows : true
          }
      },
      show: false,
      profiles: [
        {
          name: '超视界国际教育私塾微信公众号学生页面',
          skills: 'vue vue-cli vuex vue-router axios echart museui',
          content: '用户可以在此页面进行预约课程选择老师，查看预约记录，进行账户充值。查个个人相关信息等。',
          content_tit: '主要技术：',
          content1: '1.使用vue + museui框架布局',
          content2: '2.使用vuex进行组间数据共享',
          content3: '3.使用axios进行前后台数据交互'
        },
        {
          name: '超视界国际教育私塾微信公共号后台管理系统',
          skills: 'vue vue-cli vuex vue-router axios echart iview',
          content: '该项目为超视界国际教育私塾微信公共号后台管理系统。主要功能模块有学生管理，教师管理等。用户可以通过此系统对以上模块进行管理。',
          content_tit: '主要技术：',
          content1: '1.iView UI框架布局',
          content2: '2.数据可视化工具使用echats',
          content3: '3.使用axios进行前后台数据交互'
        },
        {
          name: 'crm系统',
          skills: 'jquery bootstrap',
          content: '该项目为客户信息管理系统。管理员通过此系统对客户信息进行管理。',
          content_tit: '主要技术：',
          content1: '1.使用bootstrap框架布局',
          content2: '2.数据可视化工具使用echats',
          content3: '3.使用ajax(jquery)进行前后台数据交互'
        },
        {
          name: '医耗云平台',
          skills: 'jquery miniui fileinput bootstrap',
          content: '该项目为医院供货商和医院采购管理系统。主要功能模块有财务管理，办公管理，供应链管理，公共资源管理等',
          content_tit: '主要技术：',
          content1: '1.使用miniui + bootstrap框架布局',
          content2: '2.数据可视化工具使用echats,文件上传使用fileinput插件',
          content3: '3.使用ajax(jquery)进行前后台数据交互'
        }
      ]
    }
  },
  methods: {
    timeout () {
      setTimeout(() => {
        this.show = true
      }, 1000)
    }
  },
  mounted () {
    this.timeout()
  },
  components: {
    swiper,
    swiperSlide
  }
}
</script>

<style lang="less" scoped>
@import '../less/index.less';
.info {
  .info;
  .cont {
    .cont;
    width: 100%;
    .item {
      width: 80%;
      background-color: rgba(59, 123, 13, 0.2);
      padding: 5px;
      border-radius: 15px;
      margin: 5px auto;
      @media screen and (max-width: 768px) {
        padding: 5px;
        margin: 5px auto;
      }
      &:hover {
        border-radius: 25px;
        background-color: rgba(10, 135, 87, 0.3);
      }
      p {
        text-align: left;
        text-indent: 28px;
        font-size: 1.1rem;
        line-height: 1;
        @media screen and (max-width: 768px) {
          text-indent: 12px;
          font-size: 0.7rem;
          line-height: 1.1;
        }
      }
    }
  }
}
</style>



